<template>
  <q-page class="flex flex-center">
    <div class="column">
      <div><!--- class="q-pa-md">--->
        <img alt="NAOB logo" src="../assets/naob-logo.c94a03af.svg" style="width: 200px; height: 75px">
      </div>
      <div>
        Ord
        <q-linear-progress size="25px" :value="progVal1" color="blue">
          <div class="absolute-full flex flex-center">
            <q-badge color="white" text-color="blue" :label="progress1" />
          </div>
        </q-linear-progress>
        Poeng
        <q-linear-progress size="25px" :value="progVal2" color="blue">
          <div class="absolute-full flex flex-center">
            <q-badge color="white" text-color="blue" :label="progress2" />
          </div>
        </q-linear-progress>
      </div>
      <div v-html="renderText" center></div>
      <div class="row">
        <div v-for="(letter, index) in letters" :key="index">
          <q-btn @click="addLetter(letter, index)" v-if="index!=3">{{letter}}</q-btn>
          <q-btn @click="addLetter(letter, index)" v-if="index===3" color="blue">{{letter}}</q-btn>
        </div>
      </div>
      <div class="row">
        <q-btn icon="backspace" @click="deleteLetter()"></q-btn><q-btn icon="autorenew" @click="scramble()"></q-btn><q-btn icon="check_box" @click="addWord()"></q-btn>
      </div>
      <div v-for="(word, index) in words" :key="index"><strong style='color:#152C4C'>{{word}}</strong></div>
    </div>
  </q-page>
</template>

<style>
</style>

<script>
import json from '../assets/naob_ub.json'
import bokstaver from '../assets/bokstaver.json'

export default {
  name: 'Spillet',
  props: {
  },
  data () {
    return {
      unform: '',
      letters: ['H', 'P', 'E', 'O', 'S', 'V', 'F'],
      words: [],
      okWords: {},
      totPoints: 0,
      points: 0,
      myJson: json,
      bokst: bokstaver
    }
  },
  created() {
    let letterString = this.letters.join("")
    this.myJson.words.forEach(word=>{
      if (word.length > 3) {
        let mix = true
        let has = false
        for (var i = 0; i < word.length; i++) {
          if (!letterString.includes(word[i].toUpperCase())) {
            mix = false
            break
          }
          if (word[i].toUpperCase()===this.letters[3]) has = true
        }
        if (mix && has) {
          let score = this.calculateScore(word.toUpperCase())
          this.okWords[word.toUpperCase()] = score
          this.totPoints += score
          //console.log(word.toUpperCase())
        }
      }
    })
    //console.log()
  },
  methods: {
    calculateScore(word) {
      let score = 0
      for (var i = 0; i < word.length; i++) {
        score += this.bokst[word[i]]["points"]
      }
      return score
    },
    addLetter(letter) {
      this.unform = this.unform + letter
    },
    addWord() {
      if (this.unform != "" && !this.words.includes(this.unform) && Object.keys(this.okWords).includes(this.unform)) {
        this.points += this.okWords[this.unform]
        this.words.push(this.unform)
      }
      this.unform = ""
    },
    deleteLetter() {
      this.unform = this.unform.slice(0, -1)
    },
    scramble() {
      let tempLetters = this.letters.slice(0, 3).concat(this.letters.slice(4, 7))
      for (let i = tempLetters.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        const temp = tempLetters[i];
        tempLetters[i] = tempLetters[j];
        tempLetters[j] = temp;
      }
      this.letters = tempLetters.slice(0, 3).concat(this.letters[3]).concat(tempLetters.slice(3, 6))
    },
  },
  computed: {
    renderText: function () {
      let formatted = ""
      for (var i = 0; i < this.unform.length; i++) {
        formatted += (this.unform[i]===this.letters[3] ? "<strong style='color:#A64343'>" : "<strong style='color:#152C4C'>") + this.unform[i] + "</strong>"
      }
      return formatted
    },
    progress1: function () {
      return this.words.length + " av " + Object.keys(this.okWords).length
    },
    progVal1: function () {
      return this.words.length / Object.keys(this.okWords).length
    },
    progress2: function () {
      return this.points + " av " + this.totPoints
    },
    progVal2: function () {
      return this.points / this.totPoints
    }
  }
}
</script>
